import { Component, OnInit } from '@angular/core';
import {HunkRichTextComponent} from  '../../components/hunk-richtexteditor/hunkRichTexteditor.component';
import {APIService} from  '../../globals/services/API.service';
import {MessageTypes,ErrorService,ErrorMsg} from  '../../globals/services/error.service';
import {HtmlcontentsService} from  '../../globals/services/htmlcontents.service';

@Component({
  moduleId: module.id,
  selector: 'app-kapcsolat',
  templateUrl: 'kapcsolat.component.html',
  styleUrls: ['kapcsolat.component.css'],
  directives: [HunkRichTextComponent],
  providers: [APIService]
})
export class KapcsolatComponent {

  content:any={name:'',phone:'', email:'',comments:''}
  constructor(public _HtmlcontentsService:HtmlcontentsService,
              private _APIService:APIService,
              private _ErrorService:ErrorService) {
  }

  sendMessage() {


    this._APIService.getResponse('api/kapcsolat/email/', this.content).subscribe(
        data=> {
          this._ErrorService.errorClear();
          if (data.success)
          {
            this.content={name:'',phone:'', email:'',comments:''};
            let error:ErrorMsg = {
              type: MessageTypes.mtSuccess,
              msg: [data.success]
            }
            this._ErrorService.setErrorMsg(error);
          }
        console.log(data);

        if (data.message) {
          let error:ErrorMsg = {
            type: MessageTypes.mtDanger,
            msg: data.message.errors
          }
          this._ErrorService.setErrorMsg(error);
        }




      },
        error=> {
        console.log(error);

      })

  }
}
